.albums-container {
    /* background-color: #fff; */
    width: 800px;
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    -ms-flex-direction: column;
        flex-direction: column;
    border: 7px solid #FEFCFF;
    margin-top: 20px;
}

.albums__albums {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: auto;
    margin-left: 30px;
    margin-right: 20px;
    margin-top: 13px;
}

.panel__title--albums {
    text-align: center;
    margin-top: 10px;
}

.row__button--albums {
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    background-color: #FEFCFF;
}

.row__button--albums:active,
.row__button--albums:focus {
    border-color: rgb(216, 181, 221);
    -webkit-box-shadow: 0 0 0 0.2rem rgba(233,207,236,.65);
            box-shadow: 0 0 0 0.2rem rgba(233,207,236,.65);
    background-color: #FEFCFF;
}


