.user-search-form {
    width: 780px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 10px;
}

.user-search-form__row-input {
    -ms-flex-line-pack: center;
        align-content: center;
    /* background-color: azure; */
    width: 780px;
    display: -ms-flexbox;
    display: flex;
}

.user-search-form__row-search-label-col,
.user-search-form__row-search-label,
.user-search-form__row-button-col,
.user-search-form__row-button {
    display: -ms-flexbox;
    display: flex;
    margin: auto;
}

.user-search-form__row-form-control:focus,
.user-search-form__row-button:focus {
    border-color: #E9CFEC;
    -webkit-box-shadow: 0 0 0 0.2rem rgba(233,207,236,.5);
            box-shadow: 0 0 0 0.2rem rgba(233,207,236,.5);
}

.user-search-form__row-form-control:active,
.user-search-form__row-button:active {
    border-color: rgb(216, 181, 221);
    -webkit-box-shadow: 0 0 0 0.2rem rgba(233,207,236,.65);
            box-shadow: 0 0 0 0.2rem rgba(233,207,236,.65);
}

.user-search-form__row-button {
    background-color: #F5E3FA;
}

.user-search-form__row-button:active {
    background-color: #F3D9F6;
}


